import React from 'react';
import { Block } from '@ali/4ever-cangjie';
import { IVoteData } from '../types';

export const VOTING_ACTIVE = Symbol('VOTE_ACTIVE');
export const VOTE_EDITING = Symbol('VOTE_EDITING');
export const VOTE_CANCEL_EDITING = Symbol('VOTE_CANCEL_EDITING');
export const VOTE_CANCEL_VOTING = Symbol('VOTE_CANCEL_VOTING');
export const VOTE_SAVE = Symbol('VOTE_SAVE');
export const VOTE_DELETE = Symbol('VOTE_DELETE');
export const VOTE_COPY = Symbol('VOTE_COPY');
export const VOTE_CUT = Symbol('VOTE_CUT');
export const VOTE_SUBMIT = Symbol('VOTE_SUBMIT');
export const VOTE_MODIFY = Symbol('VOTE_MODIFY');
export const VOTE_SAVE_CP = Symbol('VOTE_SAVE_CP');

interface IVoteSubmit {
  node: Block;
  voteId: string;
  optionIds: number[];
  setData: React.Dispatch<React.SetStateAction<IVoteData>>;
}

export function votingActive(node: Block) {
  return {
    type: VOTING_ACTIVE,
    payload: {
      node,
    },
  };
}

export function voteEditing(node: Block) {
  return {
    type: VOTE_EDITING,
    payload: {
      node,
    }
  }
}

export function cancelEditing(node: Block, data: IVoteData) {
  return {
    type: VOTE_CANCEL_EDITING,
    payload: {
      node,
      ...data,
    }
  }
}

export function voteSave({node, data, setData}: {
  node: Block;
  data: IVoteData,
  setData: React.Dispatch<React.SetStateAction<IVoteData>>,
}) {
  return {
    type: VOTE_SAVE,
    payload: {
      node,
      setData,
      ...data,
    }
  }
}

export function voteDelete(nodeKey: string) {
  return {
    type: VOTE_DELETE,
    payload: {
      key: nodeKey,
    }
  }
}

export function voteCopy(nodeKey: string) {
  return {
    type: VOTE_COPY,
    payload: {
      key: nodeKey,
    }
  }
}

export function voteCut(nodeKey: string) {
  return {
    type: VOTE_CUT,
    payload: {
      key: nodeKey,
    }
  }
}

export function voteSubmit(payload: IVoteSubmit) {
  return {
    type: VOTE_SUBMIT,
    payload,
  }
}

export function voteModify(nodeKey: string) {
  return {
    type: VOTE_MODIFY,
    payload: {
      key: nodeKey,
    }
  }
}

export function voteSaveCP(nodeKey: string, payload: IVoteData) {
  return {
    type: VOTE_SAVE_CP,
    payload: {
      key: nodeKey,
      payload,
    }
  }
}

export function cancelVoting(node: Block) {
  return {
    type: VOTE_CANCEL_VOTING,
    payload: {
      node,
    }
  }
}
